@import url(https://fonts.googleapis.com/css?family=Space+Mono&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color: #525B56;
  height: 100vh;
  color: #f8f8ff;
  text-align: center
}

.timeAdjusterContainer{
  display: flex;
  justify-content: space-around;
  margin-left: 35%;
  width: 30%;
  font-size: 2em;
}

.title{
  margin-top: 7%;
  font-size: 4em;
}

.timerAndButtons{
  padding-top: 3%;
  text-align: center
}
.playPauseButton{
    background-color: #525B56;
    color: #f8f8ff;
    border-style: none;
    font-size: 2em;
}
.timeContainer{
    display: inline-block;
}

.time{
    display: inline-block;
    width: 20%;

}
.timerContainer{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 15%;
    margin-bottom: 2%;
    margin-left: auto;
    margin-right: auto;
    padding: 1.5% 1%;
    border-radius: 40px;
    border-style: solid;
    border-color: rgba(48, 48, 48, 0.5);    
    border-width: 0.5em;
}

.titleSession{
    font-size: 2em;
}

.timeDisplay{
    font-family: 'Space Mono', monospace;
    font-size: 5em;
}

;
.resetButton{
    background-color: #525B56;
    color: #f8f8ff;
    border-style: none;
    font-size: 2em;
}
